<template>
  <div class="operate-task-log">
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <div style="height:400px;overflow-y: auto;" class="modelSonDiv" v-if="contentFlag1">
      <Form
        ref="formValidateModel1"
        :model="formValidateModel1"
        :rules="ruleValidate"
        :label-width="120"
      >
        <FormItem label="流程标题" prop="bpmTitle">
          <Input v-model="formValidateModel1.bpmTitle" placeholder="请输入" :disabled="this.addFlag"></Input>
        </FormItem>
        <FormItem label="标题" prop="val1">
          <Input v-model="formValidateModel1.val1" placeholder="请输入" :disabled="this.addFlag"></Input>
        </FormItem>
        <FormItem label="是否公开" prop>
          <i-switch v-model="formValidateModel1.val2" size="large" :disabled="this.addFlag">
            <span slot="true">On</span>
            <span slot="false">Off</span>
          </i-switch>
        </FormItem>
        <FormItem label="编辑内容" prop="val3">
          <Input
            v-model="formValidateModel1.val3"
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="请输入"
            :disabled="this.addFlag"
          ></Input>
        </FormItem>
        <FormItem label="上传附件">
          <showUsualFiles :file-obj="dataObj" :key="fileId" @requestUploadList="receiveUploadList"></showUsualFiles>
        </FormItem>
        <Row v-if="addFlagShow">
          <Row style="text-again:left;border-bottom:1px dashed #E8EAEC;margin-bottom:20px">追加信息</Row>
          <FormItem label="上传附件">
            <showUsualFiles
              :file-obj="dataObj2"
              :key="fileId2"
              @requestUploadList="receiveUploadList2"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容">
            <Input
              v-model="formValidateModel1.val11"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入"
            ></Input>
          </FormItem>
        </Row>
        <Modal :mask-closable="false" v-model="taskPeipleModel" width="360" class="treeBoxModel">
          <p slot="header" style="color:#f60;text-align:center">
            <span>审核人信息</span>
          </p>
          <div style="text-align:left;overflow-y:auto;height:300px">
            <FormItem prop="nextUser" :label-width="10">
              <Tree
                ref="treeRef"
                :data="data2"
                show-checkbox
                multiple
                @on-check-change="updateNextUser"
              ></Tree>
            </FormItem>
          </div>
          <div slot="footer">
            <Button
              type="primary"
              size="large"
              long
              @click="handleSubmitLast('formValidateModel1')"
              :disabled="buttonDisabled"
            >立即申请</Button>
          </div>
        </Modal>
      </Form>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="save1('formValidateModel1')">下一步</Button>
          <Button @click="closeFather">取消</Button>
        </div>
      </div>
    </div>
    <!-- ============== -->
    <div class="content" v-if="contentFlag2">
      <Form :label-width="120">
        <FormItem label="流程标题：">
          <label>{{watchTableCell.bpmTitle}}</label>
        </FormItem>
        <FormItem label="标题：">
          <label>{{watchTableCell.val1}}</label>
        </FormItem>
        <FormItem label="是否公开：">
          <label>{{watchTableCell.val2=="true"?'是':'否'}}</label>
        </FormItem>
        <FormItem label="编辑内容：">
          <label>
            <!-- {{watchTableCell.val3}} -->
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val3"
            >
              <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val3}}</p>
            </el-popover>
          </label>
        </FormItem>
        <FormItem label="附件：">
          <showUsualFiles
            :file-obj="dataObj4"
            :key="fileId4"
            @requestUploadList="receiveUploadList4"
          ></showUsualFiles>
        </FormItem>
        <FormItem label="追加附件：">
          <showUsualFiles
            :file-obj="dataObj5"
            :key="fileId5"
            @requestUploadList="receiveUploadList5"
          ></showUsualFiles>
        </FormItem>
        <FormItem label="追加内容：">
          <label>
            <!-- {{watchTableCell.val11}} -->
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val11"
            >
              <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val11}}</p>
            </el-popover>
          </label>
        </FormItem>
      </Form>
    </div>
    <!-- ===========弹框 -->
  </div>
</template>
<script>
import processMessage from "@/components/moneyRiskAssess/common/processMessage"; //过程信息
import showUsualFiles from "@/components/commonComp/showUsualFiles";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect, showUsualFiles, processMessage },
  // **************
  props: ["processdate"],
  // **************
  created() {
    // **************
    if (this.processdate) {
      if (this.processdate.modelFlag == "编辑") {
        if (
          this.processdate.addormobile == "addFlag" ||
          this.processdate.addormobile == "modifyFlag"
        ) {
          let val = this.processdate.id;
          this.addfun(val);
          this.contentFlag1 = true;
        }
      } else if (this.processdate.modelFlag == "详情") {
        let val = this.processdate.id;
        let val2 = this.processdate.busId;
        this.watchTableCellFun(val, val2);
        this.contentFlag2 = true;
      }
    }
    // ****************
    let that = this;
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/PROCESS_CHECK_STATE")
      .then(response => {
        if (response.code == "200") {
          that.PROCESS_CHECK_STATE = response.data.PROCESS_CHECK_STATE;
        }
      })
      .catch(error => {});
  },
  watch: {
    modal1(a) {
      if (a == false) {
        this.$refs["formValidateModel1"].resetFields();
        this.taskPeipleModel = false;
        this.fileId = !this.fileId;
        this.taskPeipleModel = false;
      }
    },
    "$store.state.firstLoginFlag": {
      handler(newVal) {
        this.firstLoginFlag = newVal;
      }
    }
  },
  data() {
    const validaterNextUser = (rule, value, callback) => {
      if (this.formValidateModel1.nextUser == "" && this.taskPeipleModel) {
        return callback(new Error("请选择一个人"));
      } else {
        callback();
      }
    };
    const validatertaskPeipleModelData2NextUser = (rule, value, callback) => {
      if (this.taskPeipleModelData2.nextUser == "" && this.taskPeipleModel2) {
        return callback(new Error("请选择一个人"));
      } else {
        callback();
      }
    };
    // const validaterNextStepName = (rule, value, callback) => {
    //   if (this.formValidateModel1.nextStepName == "" && this.taskPeipleModel) {
    //     return callback(new Error("请输入名称"));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      buttonDisabled:false,
      // ***************
      contentFlag1: false,
      contentFlag2: false,
      // ***************
      firstLoginFlag: this.$store.state.firstLoginFlag,
      showModal: false,
      delateId: "",
      taskPeipleModel2: false,
      taskPeipleModelData2: {
        nextUser: ""
        // nextTitel: ""
      },
      ruleValidate2: {
        // nextTitel: [
        //   {
        //     type: "string",
        //     max: 1000,
        //     message: "您超出了范围",
        //     trigger: "blur"
        //   },
        //   {
        //     required: true,
        //     message: "此项不能为空",
        //     trigger: "blur"
        //   }
        // ],
        nextUser: [
          {
            required: false,
            validator: validatertaskPeipleModelData2NextUser,
            trigger: "on-check-change"
          }
        ]
      },
      tableHeaderAppend: [
        { name: "序" },
        { name: "追加内容" },
        { name: "追加附件" },
        { name: "所属部门" },
        { name: "操作时间" },
        { name: "操作人员" }
      ],
      tableBodyAppend: [
        {
          content: "", //内容
          dept: "", //所属部门
          createTime: "", //创建时间
          creator: "" //创建人
        }
      ],
      watchAppenModelAppendFileModel: false,
      watchAppenModel: false,
      watchTableCell: {
        bpmTitle: "",
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      bpmName: "",
      appendId: "",
      appendbusId: "",
      fileId6: true,
      dataObj6: {},
      fileId5: true,
      dataObj5: {},
      fileId4: true,
      dataObj4: {},
      watchTableCellDitalModel: false,
      fileId3: true,
      dataObj3: {},
      watchFileModel: false,
      fileId2: true,
      dataObj2: {},
      buttonVal: "",
      modifyid: "",
      fileId: true,
      dataObj: {},
      uploadList6: [],
      uploadList5: [],
      uploadList4: [],
      uploadList3: [],
      uploadList2: [],
      // ===============
      uploadList: [],
      defaultList: [],
      data2: [],
      taskPeipleModel: false,
      PROCESS_CHECK_STATE: [],
      data1: [],
      ifExacte: "0", //查询方式
      minPubTime: "", //时间1
      maxPubTime: "", //时间2
      pageSize: 10,
      total: 0,
      pageNum: 1,
      addFlag: false,
      modifyFlag: false,
      modifyFlag: false,
      appendFlag: false,
      addFlagShow: false,
      modifyFlagShow: false,
      // =====================弹框一
      modal1: false,
      modalDisableFlag: true, //修改与新增弹框内容禁用标识
      formValidateModel1: {
        bpmTitle: "",
        val1: "",
        val2: true,
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "open",
        val10: "",
        val11: "",
        nextUser: "",
        appendAttachId: ""
      },
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "标题" },
        { name: "状态" },
        { name: "是否公开" },
        { name: "管理部门" },
        { name: "追加信息" },
        { name: "附件" },
        { name: "发布人" },
        { name: "发布时间" },
        { name: "更新人" },
        { name: "更新时间" }
      ],
      tableBody: [],
      formValidate: {
        value2: "",
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: []
      },
      ruleValidate: {
        bpmTitle: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "blur"
          },
          {
            type: "string",
            max: 50,
            message: "最大输入长度不能超过50字",
            trigger: "blur"
          }
        ],
        val10: [
          {
            type: "string",
            max: 1000,
            message: "您超出了范围",
            trigger: "blur"
          }
        ],
        nextUser: [
          {
            required: false,
            validator: validaterNextUser,
            trigger: "on-check-change"
          },
          { max: 250, message: "输入长度过长", trigger: "blur" }
        ],
        // nextStepName: [
        //   {
        //     required: true,
        //     validator: validaterNextStepName,
        //     trigger: "on-blur"
        //   },
        //   {
        //     type: "string",
        //     max: 1000,
        //     message: "您超出了范围",
        //     trigger: "blur"
        //   }
        // ],
        val1: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "blur"
          },
          { max: 125, message: "输入长度过长", trigger: "blur" }
        ],
        val2: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val3: [{ max: 1000, message: "输入长度过长", trigger: "blur" }],
        val11: [{ max: 1000, message: "输入长度过长", trigger: "blur" }],
        val4: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val5: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val6: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val7: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val8: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val9: [
          {
            required: false,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        Inputvalue1: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ],
        Inputvalue2: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    // **************
    closeFather() {
      this.$emit("closeModel");
    },
    // ************
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn() {
      let that = this;
      let data = {
        title: this.formValidate.val1,
        flag: this.formValidate.val3,
        ifExacte: this.ifExacte,
        minPubTime: this.minPubTime,
        maxPubTime: this.maxPubTime, //时间2 */
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
      this.axios
        .post(
          "/process/infPublicOpinionMonitoring/getInfPublicOpinionMonitoringExcelList",
          data,
          {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          }
        )
        .then(response => {
          this.download(response, "导出文档.xlsx");
        })
        .catch(error => {});
    },
    showProcess(item) {
      this.showModal = true;
      this.dataObj = {
        businessId: item.bpmBusId,
        name: "过程管理"
      };
    },
    //禁用非叶子节点
    treeNLeafNodeDisable(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children) {
          this.$set(treeDatas[i], "expand", true);
          this.$set(treeDatas[i], "disabled", true);
          this.treeNLeafNodeDisable(treeDatas[i].children);
        }
      }
    },
    watchAppenModelAppendFile(item) {
      // -----------------
      this.dataObj6 = {
        data: item,
        showFlag: false,
        disabled: false
      };
      this.fileId6 = item.busId;
      this.watchAppenModelAppendFileModel = true;
      // ---------------
    },
    watchAppendFun(a, b, c) {
      let that = this;
      let data = {
        id: c,
        relationBusId: b
      };
      data = JSON.stringify(data);
      this.axios
        .post("/process/infProcessAppend/getInfProcessAppendList", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.tableBodyAppend = response.data;
          }
        })
        .catch(error => {});
      this.watchAppenModel = true;
    },
    watchTableCellFun(valid, valid2) {
      this.axios
        .post(`/process/infPublicOpinionMonitoring/getEntityRow/${valid}`)
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.bpmTitle = response.data.bpmTitle;
            this.watchTableCell.val1 = response.data.title;
            this.watchTableCell.val2 = response.data.ifShow;
            this.watchTableCell.val3 = response.data.content;
            this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: valid2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // ---------------
      this.watchTableCellDitalModel = true;
    },
    watchFileFun(a, b, c) {
      this.dataObj3 = {
        showFlag: false,
        data: c,
        disabled: false
      };
      this.fileId3 = a;
      this.watchFileModel = true;
    },
    receiveUploadList(list) {
      this.uploadList = [];
      list.forEach(item => {
        this.uploadList.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList;
    },
    receiveUploadList2(list) {
      this.uploadList2 = [];
      list.forEach(item => {
        this.uploadList2.push(item.url);
      });
      this.formValidateModel1.appendAttachId = this.uploadList2;
    },
    receiveUploadList3(list) {
      this.uploadList3 = [];
      list.forEach(item => {
        this.uploadList3.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList3;
    },
    receiveUploadList4(list) {
      this.uploadList4 = [];
      list.forEach(item => {
        this.uploadList4.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList4;
    },
    receiveUploadList5(list) {
      this.uploadList5 = [];
      list.forEach(item => {
        this.uploadList5.push(item.url);
      });
    },
    receiveUploadList6(list) {
      this.uploadList6 = [];
      list.forEach(item => {
        this.uploadList6.push(item.url);
      });
    },
    //新增修改的
    handleSubmitLast(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          // ===============
          this.buttonDisabled = true;
          let that = this;
          let data = {
            bpmTitle: this.formValidateModel1.bpmTitle,
            bpmName: this.bpmName,
            title: this.formValidateModel1.val1,
            ifShow: this.formValidateModel1.val2,
            content: this.formValidateModel1.val3,
            nextUser: this.formValidateModel1.nextUser,
            attachId: this.formValidateModel1.attachId,
            id: this.modifyid
          };
          data = JSON.stringify(data);
          this.axios
            .post(
              "/process/infPublicOpinionMonitoring/updateInfPublicOpinionMonitoring",
              data,
              {
                headers: { "Content-Type": "application/json" }
              }
            )
            .then(response => {
              if (response.code == "200") {
                this.buttonDisabled = false;
                this.$Message.info("保存成功");
                // this.$refs[name].resetFields();
                this.modal1 = false;
                this.taskPeipleModel = false;
                // *****************
                this.closeFather() 
                // that.$router.push({
                //   path: "/processAuditExtrackApply",
                //   query: {
                //     menuCode: "110005001000"
                //   }
                // });
                // ****************
              }
            })
            .catch(error => {
              this.buttonDisabled = false;
            });
        }
      });
    },
    datechange2(a) {
      this.formValidateModel1.val2 = a;
    },
    datechange3(a) {
      this.formValidateModel1.val3 = a;
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    datechange(a) {
      this.minPubTime = a[0];
      this.maxPubTime = a[1];
    },
    addfun(id) {
      this.addFlagShow = false;
      this.modifyFlagShow = true;
      this.addFlag = false;
      this.modifyid = id;
      this.axios
        .post(`/process/infPublicOpinionMonitoring/getEntityRow/${id}`)
        .then(response => {
          if (response.code == "200") {
            this.formValidateModel1.attachId = response.data.attachId;
            this.formValidateModel1.val1 = response.data.title;
            this.formValidateModel1.val2 = JSON.parse(response.data.ifShow);
            this.formValidateModel1.val3 = response.data.content;
            this.formValidateModel1.bpmTitle = response.data.bpmTitle;
            this.dataObj = {
              data: response.data,
              showFlag: true,
              disabled: false,
              worfFlow: true
            };
            this.fileId = response.data.busId;
          }
        })
        .catch(function(error) {});
    },
    updateNextUser(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidateModel1.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.formValidateModel1.nextUser = element.code;
      });
      this.$refs["formValidateModel1"].validateField("nextUser");
    },
    updateNextUser2(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.taskPeipleModelData2.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.taskPeipleModelData2.nextUser = element.code;
      });
      this.$refs["taskPeipleModelData2"].validateField("nextUser");
    },
    save1(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          if (
            this.addFlagShow == true &&
            this.addFlag == true &&
            this.modifyFlagShow == true
          ) {
            // -----------------
            let data3 = {
              id: this.appendId,
              relationBusId: this.appendbusId,
              content: this.formValidateModel1.val11,
              attachId: this.formValidateModel1.appendAttachId,
              busId: this.busId
            };
            data3 = JSON.stringify(data3);
            this.axios
              .post(`/process/infProcessAppend/updateInfProcessAppend`, data3, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.info("保存成功");
                  this.modal1 = false;
                }
              })
              .catch(function(error) {});
            // ---------------
          } else {
            this.axios
              .get("/bpm/getNodeDefDetail?bpmType=process")
              .then(response => {
                this.bpmName = response.data[1].bpmName;
                let data = {
                  bpmName: response.data[1].bpmName,
                  nodeId: response.data[1].nodeId,
                  roleName: response.data[1].roleName,
                  bpmType: "process"
                };
                data = JSON.stringify(data);
                this.axios
                  .post("/bpm/getNextRole", data, {
                    headers: { "Content-Type": "application/json" }
                  })
                  .then(res => {
                    if (res.code == "200") {
                      this.data2 = res.data;
                      this.treeNLeafNodeDisable(this.data2);
                      this.taskPeipleModel = true;
                    }
                  })
                  .catch(error => {});
              })
              .catch(function(error) {});
            this.taskPeipleModel = true;
          }
        }
      });
    },
    rolefun() {}
  }
};
</script>
<style scoped>
.tableBox tr > td:nth-child(2) {
  min-width: 100px;
}
.tableBox tr > td:nth-child(3) {
  min-width: 160px;
}
</style>



