<template>
  <div>
    <div class="contentCellBox">
      <div class="contentCellBody">
    
            <Row>
              <table class="tableBox" style="margin-top:0">
                <thead>
                  <tr>
                    <td v-for="theads in tableHeader">{{theads.name}}</td>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td v-for="tbodys in tableBody" :title="tbodys.assistFlag?tbodys.val:''"  :class="{'textMaxWidth textOverflow':tbodys.assistFlag}">{{tbodys.val}}</td>
                  </tr>
                </tbody>
              </table>
            </Row>
         </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["dataObjj"],
  created() {
    let documentId = this.dataObjj.documentId;
    let data = {
      documentId: documentId
    };
    this.axios
      .post("/bpm/doTaskView", JSON.stringify(data), {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response && response.code == "200") {
          let tableData = JSON.parse(response.data.confirmData);
          this.tableHeader = tableData.tableHeader;
          this.tableBody = tableData.tableBody;
        }
      })
      .catch(error => {});
  },
  data() {
    return {
      tableHeader: [],
      tableBody: []
    };
  }
};
</script>


