      data = JSON.stringify(data);
      this.axios
        .post("/process/infProcessAppend/getInfProcessAppendList", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.tableBodyAppend = response.data;
          }
        })
        .catch(error => {});
      this.watchAppenModel = true;
    },
    // nextStepNameFun() {
    //   this.$refs["formValidateModel1"].validateField("nextStepName");
    // },
    watchTableCellFun(valid, valid2) {
      this.axios
        .post(`/process/infPostPersonnel/getEntityRow/${valid}?decode=1`)
        .then(response => {
          if (response.code == "200") {
            (this.watchTableCell.bpmTitle = response.data.bpmTitle),
              (this.watchTableCell.val1 = response.data.title),
              (this.watchTableCell.val2 = response.data.name),
              (this.watchTableCell.val3 = response.data.duty),
              (this.watchTableCell.val4 = response.data.tele),
              (this.watchTableCell.val5 = response.data.phone),
              (this.watchTableCell.val6 = response.data.email),
              (this.watchTableCell.val7 = response.data.qualification),
              (this.watchTableCell.val8 = response.data.workBankYears),
              (this.watchTableCell.val9 = response.data.workAntiYears),
              (this.watchTableCell.val10 = response.data.postType),
              (this.watchTableCell.val12 = response.data.ifSuspect),
              (this.watchTableCell.val13 = response.data.ifShow),
              (this.watchTableCell.val14 = response.data.content),
              (this.dataObj4 = {
                data: response.data,
                showFlag: false,
                disabled: false
              });
            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: valid2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // ---------------
      this.watchTableCellDitalModel = true;
    },
    watchFileFun(a, b, c) {
      this.dataObj3 = {
        showFlag: false,
        data: c,
        disabled: false
      };
      this.fileId3 = a;
      this.watchFileModel = true;
    },
    receiveUploadList(list) {
      this.uploadList = [];
      list.forEach(item => {
        this.uploadList.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList;
    },
    receiveUploadList2(list) {
      this.uploadList2 = [];
      list.forEach(item => {
        this.uploadList2.push(item.url);
      });
      this.formValidateModel1.appendAttachId = this.uploadList2;
    },
    receiveUploadList3(list) {
      this.uploadList3 = [];
      list.forEach(item => {
        this.uploadList3.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList3;
    },
    receiveUploadList4(list) {
      this.uploadList4 = [];
      list.forEach(item => {
        this.uploadList4.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList4;
    },
    receiveUploadList5(list) {
      this.uploadList5 = [];
      list.forEach(item => {
        this.uploadList5.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList5;
    },
    receiveUploadList6(list) {
      this.uploadList6 = [];
      list.forEach(item => {
        this.uploadList6.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList6;
    },
    // =================

    handleSubmitLast(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          // ===============
          this.buttonDisabled = true;
          let that = this;
          let data = {
            bpmTitle: this.formValidateModel1.bpmTitle,
            bpmName: this.bpmName,

            title: this.formValidateModel1.val1,
            name: this.formValidateModel1.val2,
            duty: this.formValidateModel1.val3,
            tele: this.formValidateModel1.val4,
            phone: this.formValidateModel1.val5,
            email: this.formValidateModel1.val6,
            qualification: this.formValidateModel1.val7,
            workBankYears: this.formValidateModel1.val8,
            workAntiYears: this.formValidateModel1.val9,
            postType: this.formValidateModel1.val10,
            ifSuspect: this.formValidateModel1.val12,
            ifShow: this.formValidateModel1.val13,
            content: this.formValidateModel1.val14,
            nextUser: this.formValidateModel1.nextUser,
            attachId: this.formValidateModel1.attachId,
            id: this.modifyid
          };
          data = JSON.stringify(data);
          this.axios
            .post("/process/infPostPersonnel/updateInfPostPersonnel", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.buttonDisabled = false;
                this.$Message.info("保存成功");
                // this.$refs[name].resetFields();
                this.modal1 = false;
                this.taskPeipleModel = false;
                // *****************
                this.closeFather() 
                // that.$router.push({
                //   path: "/processAuditExtrackApply",
                //   query: {
                //     menuCode: "110005001000"
                //   }
                // });
                // ****************
              }
            })
            .catch(error => {
              this.buttonDisabled = false;
            });
        }
      });
    },
    datechange2(a) {
      this.formValidateModel1.val2 = a;
    },
    datechange3(a) {
      this.formValidateModel1.val3 = a;
    },

    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    datechange(a) {
      this.minEnabledTime = a[0];
      this.maxEnabledTime = a[1];
    },
    addfun(id) {
      this.addFlagShow = false;
      this.modifyFlagShow = true;
      this.addFlag = false;
      this.modifyid = id;
      this.axios
        .post(`/process/infPostPersonnel/getEntityRow/${id}`)
        .then(response => {
          if (response.code == "200") {
            this.formValidateModel1.bpmTitle = response.data.bpmTitle;
            this.formValidateModel1.attachId = response.data.attachId;
            (this.formValidateModel1.val1 = response.data.title),
              (this.formValidateModel1.val2 = response.data.name),
              (this.formValidateModel1.val3 = response.data.duty),
              (this.formValidateModel1.val4 = response.data.tele),
              (this.formValidateModel1.val5 = response.data.phone),
              (this.formValidateModel1.val6 = response.data.email),
              (this.formValidateModel1.val7 = response.data.qualification),
              (this.formValidateModel1.val8 = response.data.workBankYears),
              (this.formValidateModel1.val9 = response.data.workAntiYears),
              (this.formValidateModel1.val10 = response.data.postType),
              (this.formValidateModel1.val12 = response.data.ifSuspect),
              (this.formValidateModel1.val13 = JSON.parse(
                response.data.ifShow
              )),
              (this.formValidateModel1.val14 = response.data.content),
              (this.dataObj = {
                data: response.data,
                showFlag: true,
                disabled: false,
                worfFlow: true
              });
            this.fileId = response.data.busId;
          }
        })
        .catch(function(error) {});
    },
    updateNextUser(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidateModel1.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.formValidateModel1.nextUser = element.code;
      });
      this.$refs["formValidateModel1"].validateField("nextUser");
    },
    updateNextUser2(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.taskPeipleModelData2.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.taskPeipleModelData2.nextUser = element.code;
      });
      this.$refs["taskPeipleModelData2"].validateField("nextUser");
    },
    save1(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          if (
            this.addFlagShow == true &&
            this.addFlag == true &&
            this.modifyFlagShow == true
          ) {
            // -----------------
            let data3 = {
              id: this.appendId,
              relationBusId: this.appendbusId,
              content: this.formValidateModel1.val11,
              attachId: this.formValidateModel1.appendAttachId,
              busId: this.busId
            };
            data3 = JSON.stringify(data3);
            this.axios
              .post(`/process/infProcessAppend/updateInfProcessAppend`, data3, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.info("保存成功");
                  this.modal1 = false;
                }
              })
              .catch(function(error) {});
            // ---------------
          } else {
            this.axios
              .get("/bpm/getNodeDefDetail?bpmType=process")
              .then(response => {
                this.bpmName = response.data[1].bpmName;
                let data = {
                  bpmName: response.data[1].bpmName,
                  nodeId: response.data[1].nodeId,
                  roleName: response.data[1].roleName,
                  bpmType: "process"
                };
                data = JSON.stringify(data);
                this.axios
                  .post("/bpm/getNextRole", data, {
                    headers: { "Content-Type": "application/json" }
                  })
                  .then(res => {
                    if (res.code == "200") {
                      this.data2 = res.data;
                      this.treeNLeafNodeDisable(this.data2);
                      this.taskPeipleModel = true;
                    }
                  })
                  .catch(error => {});
              })
              .catch(function(error) {});
            this.taskPeipleModel = true;
          }
        }
      });
    },
    rolefun() {}
  }
};
</script>
<style scoped>
.tableBox tr > td:nth-child(2) {
  min-width: 100px;
}
.tableBox tr > td:nth-child(3) {
  min-width: 160px;
}
.tableBox tr > td:nth-child(11) {
  min-width: 100px;
}
.tableBox tr > td:nth-child(14) {
  min-width: 100px;
}
</style>



