    // **************
    if (this.processdate) {
      if (this.processdate.modelFlag == "编辑") {
        if (
          this.processdate.addormobile == "addFlag" ||
          this.processdate.addormobile == "modifyFlag"
        ) {
          let val = this.processdate.id;
          this.addfun(val);
          this.contentFlag1 = true;
        }
      } else if (this.processdate.modelFlag == "详情") {
        let val = this.processdate.id;
        let val2 = this.processdate.busId;
        this.watchTableCellFun(val, val2);
        this.contentFlag2 = true;
      }
    }
    // ****************
    let that = this;
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/PROCESS_CHECK_STATE")
      .then(response => {
        if (response.code == "200") {
          that.PROCESS_CHECK_STATE = response.data.PROCESS_CHECK_STATE;
        }
      })
      .catch(error => {});
