      .post("/process/getProcessDic/region")
      .then(response => {
        if (response.code == "200") {
          that.regionArr = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/PROCESS_ASSIST_ITEM")
      .then(response => {
        if (response.code == "200") {
          that.PROCESS_ASSIST_ITEM = response.data.PROCESS_ASSIST_ITEM;
        }
      })
      .catch(error => {});
  },
