            validator: validaterNextUser,
            trigger: "on-check-change"
          }
        ],
        val10: [
          {
            pattern: /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/,
            message: "格式不正确",
            trigger: "blur"
          },
          { max: 29, message: "输入长度过长", trigger: "blur" }
        ],
        val12: [
          {
            pattern: /^[0-9]*[0-9][0-9]*$/,
            message: "格式不正确",
            trigger: "blur"
          },
          { max: 7, message: "输入长度过长", trigger: "blur" }
        ],
        val11: [
          {
            type: "string",
            max: 1000,
            message: "您超出了范围",
            trigger: "blur"
          }
        ],
        val13: [{ max: 29, message: "输入长度过长", trigger: "blur" }],
        val15: [
          {
            type: "string",
            max: 1000,
            message: "您超出了范围",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    // **************
    closeFather() {
      this.$emit("closeModel");
    },
    // ************
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn() {
      let that = this;
      let data = {
        // assistWork: this.formValidate.val1,
        executeOrg: this.formValidate.val2,
        executeProvince: this.formValidate.val3,
        executor: this.formValidate.val4,
        minAgentTime: this.minAgentTime,
        maxAgentTime: this.maxAgentTime,
        respondent: this.formValidate.val6,
        businessName: this.formValidate.val7,
        flag: this.formValidate.val8,
        manager: this.formValidate.val9,
        ifExacte: this.ifExacte,
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
      this.axios
        .post("/process/infFreezingInfo/getInfFreezingInfoExcelList", data, {
          headers: { "Content-Type": "application/json" },
          responseType: "blob"
        })
        .then(response => {
          this.download(response, "导出文档.xls");
        })
        .catch(error => {});
    },

    delPeoplefun(index) {
      this.formValidateModel1.addPeopleArr.splice(index, 1);
    },
    addpeoplefun() {
      this.formValidateModel1.addPeopleArr.push({
        bpmTitle: "",
        busId: "",
        certificateId: "",
        certificateType: "",
        clientId: "",
        clientName: "",
        id: ""
      });
    },
    showProcess(item) {
      this.showModal = true;
      this.dataObj = {
        businessId: item.bpmBusId,
        name: "过程管理"
      };
    },
    //禁用非叶子节点
    treeNLeafNodeDisable(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children) {
          this.$set(treeDatas[i], "expand", true);
          this.$set(treeDatas[i], "disabled", true);
          this.treeNLeafNodeDisable(treeDatas[i].children);
        }
      }
    },
    watchAppenModelAppendFile(a) {
      // -----------------
      this.dataObj6 = {
        data: { busId: a },
        showFlag: false,
        disabled: false
      };
      this.fileId6 = a;
      this.watchAppenModelAppendFileModel = true;
      // ---------------
    },
    watchAppendFun(a, b, c) {
      let that = this;
      let data = {
        id: c,
        relationBusId: b
      };
      data = JSON.stringify(data);
      this.axios
        .post("/process/infProcessAppend/getInfProcessAppendList", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            that.tableBodyAppend = response.data;
          }
        })
        .catch(error => {});
      this.watchAppenModel = true;
    },
    // nextStepNameFun() {
    //   this.$refs["formValidateModel1"].validateField("nextStepName");
    // },
    watchTableCellFun(valid, valid2) {
      this.axios
        .post(`/process/infFreezingInfo/getEntityRow/${valid}?decode=1`)
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.attachId = response.data.attachId;
            this.watchTableCell.bpmTitle = response.data.bpmTitle;
            this.watchTableCell.val1 = response.data.assistWork;
            this.watchTableCell.val2 = response.data.agent;
            this.watchTableCell.val3 = response.data.executeOrg;
            this.watchTableCell.val4 = response.data.executeProvince;
            this.watchTableCell.val5 = response.data.executor;
            this.watchTableCell.val6 = response.data.executorId;
            this.watchTableCell.val7 = response.data.agentTime;
            this.watchTableCell.val8 = response.data.clients;
            this.watchTableCell.val9 = response.data.caseType;
            this.watchTableCell.val10 = response.data.involvedMoney;
            this.watchTableCell.val12 = response.data.exchangeNum;
            this.watchTableCell.val13 = response.data.businessName;
            this.watchTableCell.val14 = response.data.ifShow;
            this.watchTableCell.val15 = response.data.content;
            this.watchTableCell.addPeopleArr = response.data.clients;
            this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false,
              worfFlow: true
            };
            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: valid2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false,
              worfFlow: true
            };
            this.fileId5 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // ---------------
      // ---------------
      this.watchTableCellDitalModel = true;
    },
    watchFileFun(a, b, c) {
      this.dataObj3 = {
        showFlag: false,
        data: c,
        disabled: false
      };
      this.fileId3 = a;
      this.watchFileModel = true;
    },
    receiveUploadList(list) {
      this.uploadList = [];
      list.forEach(item => {
        this.uploadList.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList;
    },
    receiveUploadList2(list) {
      this.uploadList2 = [];
      list.forEach(item => {
        this.uploadList2.push(item.url);
      });
      this.formValidateModel1.appendAttachId = this.uploadList2;
    },
    receiveUploadList3(list) {
      this.uploadList3 = [];
      list.forEach(item => {
        this.uploadList3.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList3;
    },
    receiveUploadList4(list) {
      this.uploadList4 = [];
      list.forEach(item => {
        this.uploadList4.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList4;
    },
    receiveUploadList5(list) {
      this.uploadList5 = [];
      list.forEach(item => {
        this.uploadList5.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList5;
    },
    receiveUploadList6(list) {
      this.uploadList6 = [];
      list.forEach(item => {
        this.uploadList6.push(item.url);
      });
      this.formValidateModel1.attachId = this.uploadList6;
    },
    // =================
    handleSubmitLast(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          // ===============
          this.buttonDisabled = true;
          let that = this;
          let data = {
            bpmTitle: this.formValidateModel1.bpmTitle,
            bpmName: this.bpmName,
            assistWork: this.formValidateModel1.val1,
            agent: this.formValidateModel1.val2,
            executeOrg: this.formValidateModel1.val3,
            executeProvince: this.formValidateModel1.val4,
            executor: this.formValidateModel1.val5,
            executorId: this.formValidateModel1.val6,
            agentTime: this.formValidateModel1.val7,
            clients: this.formValidateModel1.addPeopleArr, //被调查人
            caseType: this.formValidateModel1.val9,
            involvedMoney: this.formValidateModel1.val10,
            exchangeNum: this.formValidateModel1.val12,
            businessName: this.formValidateModel1.val13,
            ifShow: this.formValidateModel1.val14,
            content: this.formValidateModel1.val15,
            nextUser: this.formValidateModel1.nextUser,
            attachId: this.formValidateModel1.attachId,
            id: this.modifyid
          };

          data = JSON.stringify(data);

          this.axios
            .post("/process/infFreezingInfo/updateInfFreezingInfo", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.buttonDisabled = false;
                this.$Message.info("保存成功");
                // this.$refs[name].resetFields();
             
                this.modal1 = false;
                this.taskPeipleModel = false;
                // *****************
                this.closeFather() 
                // that.$router.push({
                //   path: "/processAuditExtrackApply",
                //   query: {
                //     menuCode: "110005001000"
                //   }
                // });
                // ****************
              }
            })
            .catch(error => {
               this.buttonDisabled = true;
            });
        }
      });
    },
    datechange2(a) {
      this.formValidateModel1.val7 = a;
    },
    datechange3(a) {
      this.formValidateModel1.val3 = a;
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    datechange(a) {
      this.minAgentTime = a[0];
      this.maxAgentTime = a[1];
    },
    addfun(id) {
      this.addFlagShow = false;
      this.modifyFlagShow = true;
      this.addFlag = false;
      this.modifyid = id;
      this.axios
        .post(`/process/infFreezingInfo/getEntityRow/${id}`)
        .then(response => {
          if (response.code == "200") {
            this.formValidateModel1.attachId = response.data.attachId;
            this.formValidateModel1.bpmTitle = response.data.bpmTitle;
            this.formValidateModel1.val1 = JSON.parse(response.data.assistWork);
            this.formValidateModel1.val2 = response.data.agent;
            this.formValidateModel1.val3 = response.data.executeOrg;
            this.formValidateModel1.val4 = response.data.executeProvince;
            this.formValidateModel1.val5 = response.data.executor;
            this.formValidateModel1.val6 = response.data.executorId;
            this.formValidateModel1.val7 = response.data.agentTime;
            this.formValidateModel1.val8 = response.data.clients;
            this.formValidateModel1.val9 = response.data.caseType;
            this.formValidateModel1.val10 = response.data.involvedMoney;
            this.formValidateModel1.val12 = response.data.exchangeNum;
            this.formValidateModel1.val13 = response.data.businessName;
            this.formValidateModel1.val14 = JSON.parse(response.data.ifShow);
            this.formValidateModel1.val15 = response.data.content;
            this.formValidateModel1.addPeopleArr = response.data.clients;
            this.dataObj = {
              data: response.data,
              showFlag: true,
              disabled: false,
              worfFlow: true
            };
            this.fileId = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
    },
    updateNextUser(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidateModel1.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.formValidateModel1.nextUser = element.code;
      });
      this.$refs["formValidateModel1"].validateField("nextUser");
    },
    updateNextUser2(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.taskPeipleModelData2.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.taskPeipleModelData2.nextUser = element.code;
      });
      this.$refs["taskPeipleModelData2"].validateField("nextUser");
    },
    save1(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          if (
            this.addFlagShow == true &&
            this.addFlag == true &&
            this.modifyFlagShow == true
          ) {
            // -----------------
            let data3 = {
              id: this.appendId,
              relationBusId: this.appendbusId,
              content: this.formValidateModel1.val11,
              attachId: this.formValidateModel1.appendAttachId,
              busId: this.busId
            };
            data3 = JSON.stringify(data3);
            this.axios
              .post(`/process/infProcessAppend/updateInfProcessAppend`, data3, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.info("保存成功");
                  this.modal1 = false;
                }
              })
              .catch(function(error) {});
            // ---------------
          } else {
            this.axios
              .get("/bpm/getNodeDefDetail?bpmType=process")
              .then(response => {
                this.bpmName = response.data[1].bpmName;
                let data = {
                  bpmName: response.data[1].bpmName,
                  nodeId: response.data[1].nodeId,
                  roleName: response.data[1].roleName,
                  bpmType: "process"
                };
                data = JSON.stringify(data);
                this.axios
                  .post("/bpm/getNextRole", data, {
                    headers: { "Content-Type": "application/json" }
                  })
                  .then(res => {
                    if (res.code == "200") {
                      this.data2 = res.data;
                      this.treeNLeafNodeDisable(this.data2);
                      this.taskPeipleModel = true;
                    }
                  })
                  .catch(error => {});
              })
              .catch(function(error) {});
            this.taskPeipleModel = true;
          }
        }
      });
    },
    rolefun() {}
  }
};
</script>
<style scoped>
.tableBox tr > td:nth-child(2) {
  min-width: 100px;
}
.tableBox tr > td:nth-child(3) {
  min-width: 160px;
}
.tableBox tr > td:nth-child(15) {
  min-width: 100px;
}
</style>



